:root {
  --font: "PT Sans", "Arial", sans-serif;
  --color-dark: #756157;
  --color-dark-hover: #615048;
  --color-light: #82b3d3;
  --color-light-hover: #68a2ca;
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsans-400.woff2") format("woff2"), url("../fonts/ptsans-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "PT Sans";
  font-display: swap;
  src: url("../fonts/ptsans-700.woff2") format("woff2"), url("../fonts/ptsans-700.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  background-color: #f2f2f2;
}

body {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 1200px;
  min-height: 100vh;
  margin: 0 auto;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  font-family: var(--font);
  color: #333333;
  background-color: #ffffff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center;
}

main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.container {
  width: 100%;
  max-width: 1240px;
  margin: 0 auto;
  padding: 0 20px;
}

.button {
  display: inline-block;
  margin: 0;
  padding: 8px;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  font-family: var(--font);
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: var(--color-dark);
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.button:hover,
.button:focus {
  background-color: #615048;
}

.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: var(--color-dark);
}

.button:disabled,
.button-disabled {
  color: #ffffff;
  background-color: #e5e5e5;
  pointer-events: none;
}

.button-light {
  background-color: var(--color-light);
}

.button-light:hover,
.button-light:focus {
  background-color: #68a2ca;
  outline: none;
}

.button-light:active {
  background-color: var(--color-light);
}

.button-light:disabled {
  background-color: #e5e5e5;
}

.button-transparent {
  background-color: transparent;
}

.button-transparent:hover {
  background-color: transparent;
  opacity: 0.6;
}

.button-transparent:focus {
  background-color: transparent;
  box-shadow: inset 0 0 0 3px #83b3d3;
}

.button-transparent:active {
  background-color: transparent;
}

.button-transparent:disabled {
  color: rgba(255, 255, 255, 0.1);
  background-color: transparent;
}

.page-title {
  margin: 0;
  font-weight: 700;
  font-size: 30px;
  line-height: 36px;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
}

.page-text {
  margin: 0;
  font-size: 22px;
  line-height: 26px;
  text-align: center;
}

.form-input {
  width: 100%;
  height: 48px;
  padding: 13px 17px 10px;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  font-family: var(--font);
  color: #000000;
  background-color: #f2f2f2;
  border: 2px solid transparent;
  border-radius: 4px;
  appearance: none;
}

.form-input[type=number],
.form-input[type=text] {
  appearance: textfield;
}

.form-input::-webkit-outer-spin-button,
.form-input::-webkit-inner-spin-button {
  appearance: none;
}

.form-input::placeholder {
  color: rgba(0, 0, 0, 0.6);
  opacity: 1;
}

.form-input:hover {
  background-color: #e6e6e6;
}

.form-input:focus {
  background-color: #e6e6e6;
  outline: none;
  box-shadow: 0 0 0 3px #83b3d3;
}

.form-input:focus:where(:not(:placeholder-shown)) {
  background-color: #f2f2f2;
  border-color: #000000;
  box-shadow: none;
}

.form-input-disabled,
.form-input:disabled {
  opacity: 0.3;
  pointer-events: none;
}

.form-input-valid {
  background-color: #ffffff;
  border: 2px solid #7db54f;
}

.form-input-error {
  border: 2px solid #ff5757;
}

.form {
  display: grid;
  gap: 22px 82px;
  grid-template-columns: repeat(2, 1fr);
}

.form-group {
  display: grid;
  margin: 0;
  padding: 0;
  border: none;
  gap: 17px;
  grid-template-columns: 120px 1fr;
}

.form-label {
  padding-top: 13px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
}

.date {
  grid-column: 1/-1;
}

.date-input {
  padding: 12px 56px 10px 18px;
  background-image: url("../img/icon-date.svg");
  background-repeat: no-repeat;
  background-position: right 20px center;
  background-size: 16px 15px;
}

.form-message {
  display: inline-block;
  margin: 5px 0 0 20px;
  font-size: 16px;
  line-height: 21px;
  color: #333333;
}

.form-message-error {
  color: #ff5757;
}

.people-input {
  padding: 13px 30px 10px;
  line-height: 20px;
  text-align: center;
}

.people-input-container {
  position: relative;
}

.people-button {
  position: absolute;
  top: 50%;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: none;
  transform: translateY(-50%);
  cursor: pointer;
}

.people-button::before,
.people-button::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 13px;
  height: 2px;
  background-color: rgba(117, 97, 87, 0.3);
  transform: translate(-50%, -50%);
}

.people-button::after {
  transform: translate(-50%, -50%) rotate(90deg);
}

.people-button-minus {
  left: 9px;
}

.people-button-minus::after {
  display: none;
}

.people-button-plus {
  right: 10px;
}

.people-button:hover::after,
.people-button:hover::before {
  background-color: #000000;
}

.people-button:focus::after,
.people-button:focus::before {
  background-color: #000000;
}

.people-button:focus {
  border: 3px solid #82b3d3;
}

.people-button:active::after,
.people-button:active::before {
  background-color: rgba(0, 0, 0, 0.2);
}

.tooltip-container {
  display: flex;
  align-items: start;
  padding-left: 6px;
}

.tooltip {
  position: relative;
  margin-left: 11px;
  padding-top: 8px;
}

.tooltip-toggle {
  width: 26px;
  height: 26px;
  background-color: #83b3d3;
  background-image: url("../img/icon-info.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 2px 12px;
  border: none;
  border-radius: 50%;
}

.tooltip-text {
  position: absolute;
  top: 100%;
  left: 50%;
  z-index: 10;
  display: none;
  width: 256px;
  margin: 15px 0 0;
  padding: 20px 20px 23px;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  background-color: #333333;
  border-radius: 10px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.3);
  transform: translateX(-50%);
}

.tooltip-text::before {
  content: "";
  position: absolute;
  top: -6px;
  left: 50%;
  z-index: -1;
  width: 12px;
  height: 12px;
  background-color: #333333;
  transform: translateX(-50%) rotate(45deg);
}

.tooltip-toggle:hover ~ .tooltip-text,
.tooltip-toggle:focus ~ .tooltip-text {
  display: block;
}

.form-button {
  margin-top: 27px;
  padding: 18px;
  font-size: 20px;
  line-height: 24px;
  border-radius: 10px;
  grid-column: 1/-1;
}

.modal-container {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  display: flex;
  background-color: rgba(242, 242, 242, 0.8);
}

.modal-container-close {
  display: none;
}

.modal {
  position: relative;
  margin: 100px auto auto;
  padding: 64px 70px 64px;
  background-color: #ffffff;
  border-radius: 30px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
}

.modal-search {
  width: 717px;
}

.modal-close-button {
  position: absolute;
  top: 63px;
  right: 52px;
  width: 52px;
  height: 52px;
  padding: 0;
  color: #000000;
  background-color: #f2f2f2;
  border: none;
  border-radius: 50%;
  cursor: pointer;
}

.modal-close-button::before,
.modal-close-button::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 17px;
  height: 2px;
  background-color: #000000;
}

.modal-close-button::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.modal-close-button::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.modal-close-button:hover {
  background-color: #e6e6e6;
}

.modal-close-button:focus {
  background-color: #e6e6e6;
  outline: none;
  /* outline: 3px solid #83b3d3; */
  box-shadow: 0 0 0 3px #83b3d3;
}

.modal-close-button:active {
  background-color: #e6e6e6;
}

.modal-close-button:active::before,
.modal-close-button:active::after {
  opacity: 0.3;
}

.modal-title {
  margin-bottom: 63px;
  padding-right: 40px;
  text-align: left;
}

.header {
  position: relative;
  padding: 0 70px;
}

.navigation {
  display: grid;
  align-items: center;
  gap: 20px;
  grid-template-columns: auto 1fr auto auto;
}

.navigation-logo {
  position: relative;
  z-index: 1;
  display: block;
  width: 140px;
  height: 70px;
  margin-bottom: -6px;
}

.navigation-logo:hover,
.navigation-logo:focus {
  outline: none;
  opacity: 0.6;
}

.navigation-logo:active {
  opacity: 0.3;
}

.navigation-logo img {
  display: block;
  width: 100%;
  height: 100%;
}

.site-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: 24px;
  gap: 32px;
}

.site-link {
  display: inline-block;
  margin-bottom: -2px;
  padding: 19px 0;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #000000;
  text-decoration: none;
  border-bottom: 2px solid transparent;
}

.site-link-current {
  border-color: #756257;
}

.site-link:hover {
  color: #756157;
}

.site-link:focus {
  color: #756157;
  outline: none;
}

.site-link:active {
  color: rgba(117, 97, 87, 0.3);
}

.user-list {
  display: flex;
  flex-wrap: wrap;
  max-width: 600px;
  max-width: 150px;
}

.user-link {
  position: relative;
  display: inline-block;
  width: 44px;
  height: 44px;
  background-repeat: no-repeat;
  background-position: center;
}

.user-link:hover,
.user-link:focus {
  outline: none;
  opacity: 0.6;
}

.user-link:active {
  opacity: 0.3;
}

.user-link-search {
  background-image: url("../img/icon-search.svg");
}

.user-link-favorites {
  background-image: url("../img/icon-heart.svg");
}

.user-favorites-num {
  position: absolute;
  top: 3px;
  right: 3px;
  display: block;
  padding: 5px;
  font-size: 10px;
  line-height: 10px;
  color: #ffffff;
  background-color: #7db54f;
  border-radius: 10px;
}

.navigation-button {
  width: 160px;
}

.intro {
  position: relative;
  padding: 50px 70px 82px;
  color: #ffffff;
  background-color: #5c84ab;
  background-image: url("../img/index-background.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.intro::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: -1px;
  left: 0;
  height: 57px;
  background-image: url("../img/divider.svg");
  background-repeat: no-repeat;
}

.intro-img {
  width: 458px;
  height: 352px;
  margin: 0 auto;
}

.intro-img img {
  display: block;
  width: 100%;
  height: 100%;
}

.advantages-description {
  padding: 70px 70px 90px;
}

.advantages-description-title {
  margin-bottom: 24px;
}

.advantages-list {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
}

.advantages-item {
  background-color: rgba(131, 179, 211, 0.2);
}

.advantages-item:nth-child(2n) {
  background-color: rgba(131, 179, 211, 0.12);
}

.advantages-item-full {
  width: 100%;
  color: #ffffff;
  background-color: var(--color-light);
}

.advantages-container {
  display: flex;
}

.advantages-item:last-child .advantages-container {
  flex-direction: row-reverse;
}

.advantages-content {
  width: 400px;
  min-height: 385px;
  padding: 115px 85px 100px;
}

.advantages-item-full .advantages-content {
  padding-top: 105px;
}

.advantages-title {
  position: relative;
  margin: 0 0 30px;
  padding-bottom: 30px;
  font-size: 24px;
  line-height: 28px;
  color: #000000;
  text-transform: uppercase;
}

.advantages-title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 60px;
  height: 2px;
  background-color: rgba(0, 0, 0, 0.3);
  transform: translateX(-50%);
}

.advantages-container .advantages-title {
  color: #ffffff;
}

.advantages-container .advantages-title::after {
  background-color: rgba(255, 255, 255, 0.3);
}

.advantages-text {
  margin: 0;
}

.advantages-image img {
  display: block;
  width: 100%;
  height: 100%;
}

.services-list {
  display: flex;
  flex-wrap: wrap;
}

.services-item {
  background-color: rgba(131, 179, 211, 0.12);
}

.services-item:nth-child(2n) {
  background-color: #ffffff;
}

.services-content {
  width: 400px;
  min-height: 385px;
  padding: 184px 85px 20px;
  background-repeat: no-repeat;
  background-position: top 80px center;
}

.services-content .advantages-title {
  padding-bottom: 0;
}

.services-content .advantages-title::after {
  display: none;
}

.services-content-home {
  background-image: url("../img/icon-house.svg");
}

.services-content-food {
  background-image: url("../img/icon-food.svg");
}

.services-content-souvenirs {
  background-image: url("../img/icon-souvenirs.svg");
}

.services-description {
  padding: 64px 70px 45px;
}

.services-description-title {
  margin-bottom: 20px;
}

.services-list {
  display: flex;
  flex-wrap: wrap;
}

.services-item {
  background-color: rgba(131, 179, 211, 0.12);
}

.services-item:nth-child(2n) {
  background-color: #ffffff;
}

.services-content {
  width: 400px;
  min-height: 385px;
  padding: 184px 85px 20px;
  text-align: center;
  background-repeat: no-repeat;
  background-position: top 80px center;
}

.services-title {
  margin: 0 0 30px;
  font-size: 24px;
  line-height: 28px;
  color: #000000;
  text-transform: uppercase;
}

.services-content-home {
  background-image: url("../img/icon-house.svg");
}

.services-content-food {
